import { defineComponent, ref, reactive } from 'vue';
import { login } from '@/api';
import { useRouter } from 'vue-router';
export default defineComponent({
  name: 'Home',
  components: {},
  setup: function setup() {
    var managerName = ref('');
    var managerCode = ref('');
    var securityCode = ref('');
    var baseCodeUrl = window.location.origin + '/service-core/rest' + '/userInfo/getVerifyCode';
    var codesrc = ref(baseCodeUrl);
    var params = reactive({
      'managerName': '',
      'managerCode': ''
    });
    var router = useRouter();
    var dialogVisible = ref(false);
    var message = ref('');
    var resultcode = ref();

    var logIn = function logIn() {
      params.managerName = managerName.value;
      params.managerCode = managerCode.value; //params.securityCode=securityCode.value

      login(securityCode.value, params).then(function (response) {
        var res = response.data;
        resultcode.value = res.code;
        message.value = res.message;
        console.log(res.code, res.message);

        if (res.code == '200') {
          console.log(res.code, res.message);
          router.push({
            name: 'home'
          });
        } else {
          dialogVisible.value = true; //alert('网络异常，请稍后重试')
        }
      }).catch(function (err) {
        return console.log(err);
      });
    };

    var goHome = function goHome() {
      dialogVisible.value = false;
      console.log('22222'); //getpro()   
      //console.log('666666666666',paramsValue)
    };

    var freshCode = function freshCode() {
      codesrc.value = baseCodeUrl + "?time=" + new Date().getTime();
      console.log(codesrc.value);
    };

    return {
      managerName: managerName,
      managerCode: managerCode,
      securityCode: securityCode,
      params: params,
      logIn: logIn,
      dialogVisible: dialogVisible,
      goHome: goHome,
      message: message,
      codesrc: codesrc,
      freshCode: freshCode
    };
  }
});